 import jsPDF from 'jspdf';
import QRCode from 'qrcode';

export const generateCertificate = async (
  studentName: string,
  programName: string,
  date: string,
  venue: string,
  uniqueId: string
): Promise<Blob> => {
  const pdf = new jsPDF({
    orientation: 'landscape',
    unit: 'mm',
    format: 'a4'
  });

  // Colors matching the theme
  const darkGreen = [0, 77, 64]; // Dark teal green
  const lightGreen = [76, 175, 80]; // Light green
  const gold = [255, 193, 7]; // Gold
  const darkGold = [255, 152, 0]; // Darker gold
  const lightGray = [245, 245, 245]; // Light background

  // Set background to light gray
  pdf.setFillColor(...lightGray);
  pdf.rect(0, 0, 297, 210, 'F');

  // Main certificate background (white)
  pdf.setFillColor(255, 255, 255);
  pdf.rect(15, 15, 267, 180, 'F');

  // Outer border
  pdf.setDrawColor(...darkGreen);
  pdf.setLineWidth(1);
  pdf.rect(15, 15, 267, 180);

  // Inner decorative border
  pdf.setDrawColor(...gold);
  pdf.setLineWidth(0.5);
  pdf.rect(20, 20, 257, 170);

  // Top left diagonal section (dark green)
  pdf.setFillColor(...darkGreen);
  pdf.setDrawColor(...darkGreen);
  pdf.setLineWidth(0);
  // Create triangle using lines
  pdf.lines([[0, 65], [65, -65], [-65, 0]], 15, 15, [1, 1], 'F');

  // Top left diagonal gold stripe
  pdf.setFillColor(...gold);
  pdf.setDrawColor(...gold);
  pdf.lines([[0, 55], [55, -55], [-55, 0]], 15, 15, [1, 1], 'F');

  // Bottom right diagonal section (dark green)
  pdf.setF
  pdf.triangle(goldStripe1[0][0], goldStripe1[0][1], 
              goldStripe1[1][0], goldStripe1[1][1], 
              goldStripe1[2][0], goldStripe1[2][1], 'F');

  // Bottom right diagonal section (dark green)
  pdf.setFillColor(...darkGreen);
  const bottomRightPoints = [
    [282, 195], [217, 195], [282, 130]
  ];
  pdf.triangle(bottomRightPoints[0][0], bottomRightPoints[0][1], 
              bottomRightPoints[1][0], bottomRightPoints[1][1], 
              bottomRightPoints[2][0], bottomRightPoints[2][1], 'F');

  // Bottom right diagonal gold stripe
  pdf.setFillColor(...gold);
  const goldStripe2 = [
    [282, 195], [227, 195], [282, 140]
  ];
  pdf.triangle(goldStripe2[0][0], goldStripe2[0][1], 
              goldStripe2[1][0], goldStripe2[1][1], 
              goldStripe2[2][0], goldStripe2[2][1], 'F');

  // Award badge (circle with ribbon)
  pdf.setFillColor(...gold);
  pdf.circle(45, 45, 15, 'F');
  pdf.setFillColor(...darkGreen);
  pdf.circle(45, 45, 12, 'F');

  // Award badge text
  pdf.setTextColor(255, 255, 255);
  pdf.setFontSize(8);
  pdf.setFont('helvetica', 'bold');
  pdf.text('2024', 45, 42, { align: 'center' });
  pdf.text('AWARD', 45, 48, { align: 'center' });

  // Award ribbon
  pdf.setFillColor(...gold);
  pdf.rect(40, 55, 10, 15, 'F');
  pdf.triangle(40, 70, 45, 75, 50, 70, 'F');

  // Main title "CERTIFICATE"
  pdf.setTextColor(...darkGreen);
  pdf.setFontSize(36);
  pdf.setFont('helvetica', 'bold');
  pdf.text('CERTIFICATE', 148.5, 70, { align: 'center' });

  // Subtitle "OF APPRECIATION"
  pdf.setFillColor(...darkGreen);
  pdf.rect(120, 75, 57, 8, 'F');
  pdf.setTextColor(255, 255, 255);
  pdf.setFontSize(12);
  pdf.setFont('helvetica', 'bold');
  pdf.text('OF APPRECIATION', 148.5, 80, { align: 'center' });

  // Student name in script font style
  pdf.setTextColor(...darkGreen);
  pdf.setFontSize(32);
  pdf.setFont('helvetica', 'bolditalic');
  pdf.text(studentName, 148.5, 110, { align: 'center' });

  // Underline for name
  pdf.setDrawColor(...darkGreen);
  pdf.setLineWidth(0.5);
  const nameWidth = pdf.getTextWidth(studentName) * 0.35; // Approximate width
  pdf.line(148.5 - nameWidth, 115, 148.5 + nameWidth, 115);

  // Program description
  pdf.setTextColor(100, 100, 100);
  pdf.setFontSize(11);
  pdf.setFont('helvetica', 'normal');
  const description = `has successfully completed the ${programName} program`;
  pdf.text(description, 148.5, 130, { align: 'center' });
  
  pdf.text(`conducted at ${venue}`, 148.5, 140, { align: 'center' });
  pdf.text(`on ${new Date(date).toLocaleDateString('en-US', { 
    year: 'numeric', 
    month: 'long', 
    day: 'numeric' 
  })}`, 148.5, 150, { align: 'center' });

  // Signature section
  pdf.setDrawColor(...darkGreen);
  pdf.setLineWidth(0.5);
  pdf.line(50, 175, 120, 175); // Signature line

  pdf.setTextColor(...darkGreen);
  pdf.setFontSize(10);
  pdf.setFont('helvetica', 'bold');
  pdf.text('DIRECTOR\'S SIGNATURE', 85, 185, { align: 'center' });

  // Date section
  const formattedDate = new Date(date).toLocaleDateString('en-GB').replace(/\//g, ' / ');
  pdf.text(formattedDate, 230, 175, { align: 'center' });
  pdf.line(200, 175, 260, 175); // Date line
  pdf.text('DATE', 230, 185, { align: 'center' });

  // Certificate ID with QR code
  try {
    const qrCodeDataUrl = await QRCode.toDataURL(`https://radialcode.com/verify/${uniqueId}`, {
      width: 80,
      margin: 1,
      color: {
        dark: '#004D40',
        light: '#FFFFFF'
      }
    });
    pdf.addImage(qrCodeDataUrl, 'PNG', 250, 45, 20, 20);
  } catch (error) {
    console.error('Error generating QR code:', error);
  }

  // Certificate ID
  pdf.setTextColor(150, 150, 150);
  pdf.setFontSize(8);
  pdf.setFont('helvetica', 'normal');
  pdf.text(`Certificate ID: ${uniqueId}`, 20, 190);

  // Decorative corner elements
  pdf.setFillColor(...gold);
  // Top right corner
  pdf.rect(270, 20, 8, 2, 'F');
  pdf.rect(270, 25, 8, 2, 'F');
  pdf.rect(270, 30, 8, 2, 'F');
  
  // Bottom left corner
  pdf.rect(20, 175, 8, 2, 'F');
  pdf.rect(20, 180, 8, 2, 'F');
  pdf.rect(20, 185, 8, 2, 'F');

  return pdf.output('blob');
};

export const generateUniqueId = (): string => {
  return 'RC-' + Date.now().toString(36).toUpperCase() + '-' + Math.random().toString(36).substring(2, 7).toUpperCase();
};